<template>
  <div>
    <el-card>
       <el-table
        :data="tableData"
        style="width: 1000px">
        <el-table-column
          prop="userName"
          label=""
          width="180">
        </el-table-column>
        <el-table-column
          prop="userMobile"
          label=""
          width="240">
        </el-table-column>
        <el-table-column
          prop="personnelType"
          label=""
          width="200">
          <template slot-scope="scope">
            <p>{{scope.row.personnelType === 'ORDINARY' ? '' : ''}}</p>
          </template>
        </el-table-column>
          
        <el-table-column
          prop="personnelStatus"
          label=""
          width="200"
        >
          <template slot-scope="scope">
            <p :style="{color: scope.row.personnelStatus === 'OK'  ? '#408fff' : '#ff9900'}">{{scope.row.personnelStatus === 'OK' ? '' : ''}}</p>
          </template>
        </el-table-column>
        <el-table-column
          prop="date"
          label=""
          >
          <template slot-scope="scope">
            <el-button type="danger" v-if="scope.row.personnelStatus === 'OK' && scope.row.personnelType !== 'ORDINARY'" @click="transfer(scope.row)" :disabled="tableData.length<=1"></el-button>
            <el-button type="primary" v-if="scope.row.personnelStatus !== 'OK' && scope.row.personnelType === 'ORDINARY'" @click="check(scope.row)"></el-button>
            <el-button type="warning" v-if="scope.row.personnelStatus === 'OK' && scope.row.personnelType === 'ORDINARY'" @click="unBind(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="20%"
    >
      <div style="display:flex;align-items:center">
        <p style="margin-right:10px"></p>
        <el-select v-model="transferValue" placeholder="">
          <el-option
            v-for="(item,index) in options" :key="index"
            v-if="item.userId !== $store.state.userData.userId"
            :label="item.userName"
            :value="item.userId">
          </el-option>
        </el-select>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="transferOne"> </el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
export default {
  data(){
    return{
      tableData: [],
      dialogVisible:false,
      options:[],
      transferValue:''
    }
  },
  methods:{
    transferOne(){
      const params = {
        userId:this.$store.state.userInfo.userId,
        orgId:this.$store.state.userData.orgId,
        uid:this.transferValue,
      }
      this.$post('/my/user/transfer',params,(res)=>{
        this.$message.success('')
        this.$router.push('/index/my')
      })
    },
    transfer(){
      this.dialogVisible = true
      const params = {
        orgId:this.$store.state.userData.orgId,
        userId:this.$store.state.userInfo.userId,
      }
      this.$post('/my/user/persons',params,(res)=>{
        this.options = res
      })
    },
    check(item){
      this.$confirm(`${item.userName}-${item.userMobile}`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.checkAjax(item,'OK',(res)=>{
            this.$message.success('')
            this.getList()
          })
        }).catch(() => {
          this.checkAjax(item,'NO',(res)=>{
            this.$message('')
            this.getList()
          })         
        });
    },
    unBind(item){
      this.$confirm(`${item.userName}-${item.userMobile}?`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          const params = {
            orgId:this.$store.state.userData.orgId,
            uid:item.userId,
            userId:this.$store.state.userInfo.userId
          }
          this.$post('/my/user/unbind',params,(res)=>{
            this.$message.success('')
            this.getList()
          })
        }).catch(() => {
             
        });
    },
    checkAjax(item,status,fun){
      const params = {
        userId:this.$store.state.userInfo.userId,
        orgId:this.$store.state.userData.orgId,
        uid:item.userId,
        status
      }
      this.$post('/my/user/audit',params,(res)=>{
          fun()
      })
    },
    // unBind(orgId,uid){
      
    // },
    getList(){
      const params = {
        orgId:this.$store.state.userData.orgId,
        userId:this.$store.state.userInfo.userId
      }
      this.$post('/my/user/manager',params,(res)=>{
        this.tableData = res.data
      })
    }
  },
  mounted(){
    this.getList()
  }
}
</script>

<style>

</style>


