<template>
  <div>
    <div class="content">
      <div class="input">
        <p></p>
        <el-input placeholder="" style="width: 420px;" v-model="password" type="password"></el-input>
      </div>
        <div class="input">
          <p></p>
          <el-input placeholder="" style="width: 420px;" v-model="mobile" type="number"></el-input>
        </div>
      <div class="input">
        <p></p>
        <el-input placeholder="" style="width: 420px;" v-model="vcode" type="number">
          <template slot="append">
            <el-button @click="sendCode()">{{timeCom}}</el-button>
          </template>
        </el-input>
      </div>
      <el-button type="primary" style="width:190px;margin-top:40px" @click="change"></el-button>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      password:'',
      vcode:'',
      isSend:false,  //
      isSendTime:0,  //
      time:'',
      mobile:'',
    }
  },
  computed:{
    timeCom(){
      if(!this.isSend){
        return ''
      }else{
        return `${this.isSendTime}`
      }
    },
  },
  methods:{
    sendCode(){
      if(!this.isSend){
        let reg = /^[1]([3-9])[0-9]{9}$/;
        if(this.mobile === ""){
          this.$message('')
          return
        }
        if (!reg.test(this.mobile)){
          this.$message('')
          return
        }
        let mobiles = this.$store.state.userInfo.userMobile;
        if(mobiles==this.mobile){
          this.$message('');
          return
        }
        const params = {
          mobile:this.mobile
        }
        this.isSendTime = 60
        this.$post('/org/send/code',params,(res)=>{
          this.$message.success('')
          this.time = setInterval(()=>{
            this.isSendTime --
            if(this.isSendTime === 0){
              this.isSend = false
              clearInterval(this.time)
              this.isSendTime = 60
            }
          },1000)
          this.isSend = true
        })   
      }else{
        this.$message.warning(`${this.isSendTime}`)
      }
    },
    change(){
      if(this.password==''){
        this.$message.warning(``);
        return
      }
      if(this.mobile==''){
        this.$message.warning(``);
        return
      }
      if(this.vcode==''){
        this.$message.warning(``);
        return
      }
      const params = {
        userId:this.$store.state.userInfo.userId,
        userPassword:this.password,
        mobile:this.mobile,
        vcode:this.vcode
      }
      this.$post('/my/update/mobile',params,(res)=>{
        this.$message.success('')
        this.$router.push('/index/my')
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.content{
  @include flex(column);
  .input{
    width: 540px;
    @include flex(null,space-between);
    margin:20px 0 ;
  }
}
</style>


