<template>
  <div>
    <Head />
    <Banner title="" content=""/>
    <div class="center-card">
      <div class="card-form">
        <p class="form-p"><span class="login" @click="goLogin"></span></p>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="demo-ruleForm" style="margin-top:20px">
          <el-form-item prop="name">
            <el-input v-model="ruleForm.name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input v-model="ruleForm.password" placeholder="" type="password"></el-input>
          </el-form-item>
          <el-form-item prop="password2">
            <el-input v-model="ruleForm.password2" placeholder="" type="password"></el-input>
          </el-form-item>
          <el-form-item prop="mobile">
            <el-input v-model="ruleForm.mobile" placeholder="" type="number"></el-input>
          </el-form-item>
          <el-form-item prop="code" style="margin-top:30px">
            <el-input placeholder="" v-model="ruleForm.code">
              <template slot="append">
                <el-button @click="sendCode">{{timeCom}}</el-button>
              </template>
            </el-input>
          </el-form-item>
          <el-form-item>
            <div class="read">
              <el-checkbox v-model="isRead" @click="isRead = !isRead"></el-checkbox>
              <a href="#">-</a>
            </div>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')" size="medium" style="width:100%"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import Head from './../../components/Head'
import Banner from './../../components/Banner'
export default {
  data(){
    let nameCheck = (rule,value,callback) =>{
      if (value === '') {
          callback(new Error(''));
      } else if (!/^[\u4e00-\u9fa5]{2,6}$/.test(value)) {
          callback(new Error('!'));
      } else {
          callback();
      }
    } 
    let passwordCheck1 = (rule,value,callback) =>{
      if (value === '') {
          callback(new Error(''));
      } else if (!/^(?![\d]+$)(?![a-zA-Z]+$)(?![^\da-zA-Z]+$).{8,20}$/.test(value)) {
          callback(new Error('8-20!'));
      } else {
          callback();
      }
    }
    let passwordCheck2 = (rule,value,callback) =>{
      if (value === '') {
          callback(new Error(''));
      } else if (value !== this.ruleForm.password) {
          callback(new Error('!'));
      } else {
          callback();
      }
    }
    return{
      ruleForm:{
        name:'',
        password:'',
        password2:'',
        mobile:'',
        code:'',
      },
      rules:{
        name: [
            // { required: true, message: '', trigger: 'blur' },
            { validator: nameCheck, trigger: 'blur' },
        ],
        password: [
            { validator: passwordCheck1, trigger: 'blur' },
        ],
        password2: [
            { validator: passwordCheck2,  trigger: 'blur' },
        ],
        mobile: [
            { required: true, message: '', trigger: 'blur' },
        ],
        code:[
          { required: true, message: '', trigger: 'blur' },
        ]
      },
      isRead:false,
      isSend:false,  //
      isSendTime:0,  //
      time:'',
    }
  },
  computed:{
    timeCom(){
      if(!this.isSend){
        return ''
      }else{
        return `${this.isSendTime}`
      }
    },
  },
  methods:{
    submitForm(formName) {
      if(this.isRead){
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let { name , password , password2 , mobile , code} = this.ruleForm
            const params = {
              userName:name,
              userMobile:mobile,
              userPassword:password,
              confirmPassword:password2,
              vcode:code,
            }
            this.$post('/org/user/register',params,(res)=>{
              this.$message.success('')
              this.$cookies.set('userInfo', res,'3h')
              this.$store.commit('userInfo',res)
              
              const params = {
                userId:this.$store.state.userInfo.userId
              }
              this.$post('/my/user/info',params,(res)=>{
                
                this.$store.commit('userData',res)
                this.$cookies.set('userData', res,'3h')
                if(!res.orgCode){
                  this.$router.push('/agency')
                  return
                }
                if(res.personnelStatus === 'OK'){
                  this.$router.push('/index')
                }else{
                  this.$router.push('/applystatus')
                }
                
              })
            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      }else{
        this.$message('')
      }
      
    },
    goLogin(){
      this.$router.push('/login')
    },
    sendCode(){
      if(!this.isSend){
        let reg = /^[1]([3-9])[0-9]{9}$/
        if (!reg.test(this.ruleForm.mobile)){
          this.$message('')
          return
        }
        const params = {
          mobile:this.ruleForm.mobile
        }
        this.isSendTime = 60
        this.$post('/org/send/code',params,(res)=>{
          this.$message.success('')
          this.time = setInterval(()=>{
            this.isSendTime --
            if(this.isSendTime === 0){
              this.isSend = false
              clearInterval(this.time)
              this.isSendTime = 60
            }
          },1000)
          this.isSend = true
        })   
      }else{
        this.$message.warning(`${this.isSendTime}`)
      }
    },
  },
  components:{
    Head,
    Banner
  }
}
</script>

<style lang="scss" scoped>
.center-card{
  .card-form{
    width: 420px;
    margin:0 auto;
    text-align: center;
    margin-top: 10px;
    .login{
      color: #408fff;
      cursor: pointer;
    }
    .form-p{
      padding-top: 40px;
    }
    .read{
      display: flex;
      flex-direction: column;
      justify-content: f;
      align-items: end;
    }
  }
}
</style>


